import React, { useEffect, useState } from "react";
import css from "./style.module.css";
import Button from "../../components/General/Button";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";
import * as actions from "../../redux/action/loginActions";
import Spinner from "../../components/General/Spinner";

const LoginPage = (props) => {
  const [form, setForm] = useState({ email: "", password: "" });

  const login = () => {
    props.login(form.email, form.password);
  };
  const changeEmail = (e) => {
    const newEmail = e.target.value;
    setForm((formBefore) => ({
      email: newEmail,
      password: formBefore.password,
    }));
  };
  const changePass = (e) => {
    const newPassword = e.target.value;
    setForm((formBefore) => ({
      email: formBefore.email,
      password: newPassword,
    }));
  };

  return (
    <div className={css.Login}>
      {props.userId && <Redirect to="/orders" />}
      <input type="text" onChange={changeEmail} placeholder="Имейл хаяг" />
      <input
        type="password"
        onChange={changePass}
        name="password"
        placeholder="Нууц үг"
      />
      {props.loggingIn && <Spinner />}
      {props.firebaseError && (
        <div style={{ color: "red" }}>{props.firebaseError.message}</div>
      )}
      <Button text="НЭВТРЭХ" clicked={login} buttonType="Success" />
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    loggingIn: state.signupLoginReducer.loggingIn,
    firebaseError: state.signupLoginReducer.firebaseError,
    userId: state.signupLoginReducer.userId,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    login: (email, password) => dispatch(actions.loginUser(email, password)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(LoginPage);
