import React, { useEffect, useState, Suspense } from "react";
import { Redirect } from "react-router-dom";
import { Route, Switch } from "react-router-dom";
import { connect } from "react-redux";

import Style from "./style.module.css";
import Toolbar from "../../components/Toolbar";
import Sidebar from "../../components/Sidebar";
import ShippingPage from "../ShippingPage";
import LoginPage from "../loginPage";
import Logout from "../../components/Logout";
import * as actions from "../../redux/action/loginActions";
import BurgerContext from "../../context/BurgerContext";

const BurgerPage = React.lazy(() => {
  return import("../BurgerPage");
});

const OrderPage = React.lazy(() => {
  return import("../OrderPage");
});

const SingUpPage = React.lazy(() => {
  return import("../SignUpPage");
});

const App = (props) => {
  const [showSidebar, setShowSidebar] = useState(false);

  const toggleSidebar = () => {
    setShowSidebar((prevShowSidebar) => !prevShowSidebar);
  };

  useEffect(() => {
    const token = localStorage.getItem("token");
    const userId = localStorage.getItem("userId");
    const expireDate = localStorage.getItem("expireDate");

    const refreshToken = localStorage.getItem("refreshToken");

    if (token) {
      if (expireDate > new Date()) {
        // Хугацаа нь дуусаагүй токен байна
        props.autologin(token, userId);

        // Токен хүчингүй болоххд үлдэж байгаа хугацааг тооцож
        // тэр хугацааны дараа автоматаар logout хийнэ.

        props.autoLogoutAfterMillisec(
          expireDate.getTime() - new Date().getTime()
        );
      } else {
        // token хугацаа нь дууссан
        props.logout();
      }
    }
  }, []);

  return (
    <div>
      <Toolbar toggleSidebar={toggleSidebar} />
      <Sidebar showSidebar={showSidebar} toggleSidebar={toggleSidebar} />

      <main className={Style.Content}>
        <Suspense fallback={<div>Түр хүлээнэ үү...</div>}>
          {props.userId ? (
            <Switch>
              <Route path="/logout" component={Logout} />
              <Route path="/ship" component={ShippingPage} />
              <Route path="/orders" component={OrderPage} />
              <Route path="/">
                <BurgerContext.Provider value={"" + showSidebar}>
                  <BurgerPage />
                </BurgerContext.Provider>
              </Route>
            </Switch>
          ) : (
            <Switch>
              <Route path="/login" component={LoginPage} />
              <Route path="/signup" component={SingUpPage} />
              <Redirect to="/login" />
            </Switch>
          )}
        </Suspense>
      </main>
    </div>
  );
};
const mapStateToProps = (state) => {
  return {
    userId: state.signupLoginReducer.userId,
  };
};
const mapDispatchToProps = (dispatch) => {
  return {
    autologin: (token, userId) => dispatch(actions.loginSuccess(token, userId)),
    logout: () => dispatch(actions.logout()),
    autoLogoutAfterMillisec: (ms) =>
      dispatch(actions.autoLogoutAfterMillisec(ms)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(App);
