import React, { useState, useEffect, useRef } from "react";
import { connect } from "react-redux";
import Button from "../General/Button";
import Spinner from "../General/Spinner";
import css from "./style.module.css";

import * as actions from "../../redux/action/orderActions";
import { useHistory } from "react-router-dom";

const ContactData = (props) => {
  const history = useHistory();
  const [name, setName] = useState(null);
  const [city, setCity] = useState(null);
  const [street, setStreet] = useState(null);

  const dunRef = useRef();

  useEffect(() => {
    console.log("Contact Data effect");
    if (props.newOrderStatus.finished && !props.newOrderStatus.error) {
      history.replace("/orders");
    }
    return () => {
      // Цэвэрлэгч функц... Захиалгыг буцаагаад хоосолно. Дараагийн захиалгад бэлдэнэ.
      console.log("Order Clearing");
      props.clearOrder();
    };
  }, [props.newOrderStatus.finished]);

  const changeName = (e) => {
    if (dunRef.current.style.color === "red")
      dunRef.current.style.color = "green";
    else dunRef.current.style.color = "red";
    setName(e.target.value);
  };
  const changeStreet = (e) => {
    setStreet(e.target.value);
  };
  const changeCity = (e) => {
    setCity(e.target.value);
  };

  const saveOrder = () => {
    const newOrder = {
      userId: props.userId,
      orts: props.ingridients,
      dun: props.price,
      hayag: {
        name: name,
        city: city,
        street: street,
      },
    };
    props.saveOrderAction(newOrder);
  };

  console.log(props);
  return (
    <div className={css.ContactData}>
      <div ref={dunRef}>
        <b style={{ fontSize: "16px" }}> ДҮН : {props.price}</b>
      </div>

      <div>
        {props.newOrderStatus.error &&
          `Захиалгыг хадгалах явцад алдаа гарлаа : ${props.newOrderStatus.error}`}
      </div>

      {props.newOrderStatus.saving ? (
        <Spinner />
      ) : (
        <div>
          <input
            onChange={changeName}
            type="text"
            name="name"
            placeholder="Таны нэр"
          />
          <input
            onChange={changeStreet}
            type="text"
            name="street"
            placeholder="Таны гэрийн хаяг"
          />
          <input
            onChange={changeCity}
            type="text"
            name="city"
            placeholder="Таны хот"
          />
          <Button text="ИЛГЭЭХ" buttonType="Success" clicked={saveOrder} />
        </div>
      )}
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    price: state.burgerReducer.totalPrice,
    ingridients: state.burgerReducer.ingridients,
    newOrderStatus: state.orderReducer.newOrder,
    userId: state.signupLoginReducer.userId,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    saveOrderAction: (newOrder) => dispatch(actions.saveOrder(newOrder)),
    clearOrder: () => dispatch(actions.clearOrder()),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(ContactData);
